@media (max-width: 575.98px) {
    header {
        .nav-links {
            display: none;
        }

        svg {
            display: block;
        }
    }

    aside {
        display: block;
    }

    main {
        flex-direction: column;

        .controls-cont {
            width: 100vw;
            height: 55vh;
        }

        .burger-cont {
            width: 100vw;
            height: 45vh;

            .burger {
                width: 80%;
            }
        }
    }
}

